import { Component, OnInit } from '@angular/core';
import { OwlModule } from 'ngx-owl-carousel';

import { News } from './../news';
import { NewsService } from './../services/news.service';
import { Event } from './../event';
import { EventService } from './../services/event.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  myCarouselImages =["./../../assets/img/clients/c1-1.png", "./../../assets/img/clients/c1-2.png",
                     "./../../assets/img/clients/c1-3.png", "./../../assets/img/clients/c1-4.png",
                     "./../../assets/img/clients/c1-5.png", "./../../assets/img/clients/c1-6.png",
                     "./../../assets/img/clients/c1-3.png"];
  myCarouselOptions={items: 7, dots: true, nav: true};

  public id: number;
  public loggedInValue: string;
  public loggedIn = false;
  public adminValue: string;
  public admin: boolean;
  news: Array<object>;
  events: Array<object>;

  constructor(private newsService: NewsService,private eventService: EventService) {
    this.id = +sessionStorage.getItem('id');
    this.loggedInValue = sessionStorage.getItem('loggedInValue');
    if(this.loggedInValue === 'true'){
      this.loggedIn = true;
    }
    this.adminValue = sessionStorage.getItem('admin');
    if(this.adminValue === 'true'){
      this.admin = true;
    }
    this.indexNews();
    this.indexEvent();
  }

  ngOnInit() {
  }
  indexNews(){
    this.newsService.indexNews()
      .subscribe(response => {
        this.news = response;
      })
  }

  indexEvent(){
    this.eventService.indexEvent()
      .subscribe(response => {
        this.events = response;
      })
  }
}
